<h1 id="android-introduction" tabindex="-1"><a class="header-anchor" href="#android-introduction" aria-hidden="true">#</a> Android Introduction</h1>
<p>Android is an operating system initially released for smartphones, but these days it supports tablets, smart watches, cars and some other things as well. Although being an open source project developed Google, Google seem to control the project just as good they would control a closed source project. Let's take a look at how!</p>
